import {
    Streamlit,
    StreamlitComponentBase,
    withStreamlitConnection,
} from "streamlit-component-lib"
import React from "react"
import PlayPause from "./PlayPause";


class PlayPauseButton extends StreamlitComponentBase {
    state = {showPlayButton: true};

    render() {
        const {showPlayButton} = this.state;
        return (
            <div>
                <button
                    onClick={
                        () => {
                            this.setState({
                                showPlayButton: !showPlayButton
                            })
                            Streamlit.setComponentValue([true])
                        }
                    }
                    style={{
                        border: "none",
                        backgroundColor: "#000",
                        cursor: "pointer",
                        height: 60,
                        width: 90,
                        outline: "none",
                        borderRadius: "50px",
                    }}
                >
                    <PlayPause buttonToShow={showPlayButton ? "play" : "pause"}/>
                </button>
            </div>
        );
    }
}

export default withStreamlitConnection(PlayPauseButton)
